import React from "react";
import styled from "styled-components";
import { Link } from "gatsby";

const Nav = styled.nav`
  ul {
    padding: 0;
    margin: 0;
    width: 100vw;
    max-width: 414px;
    li {
      list-style: none;
      display: inline;
      font-weight: bold;
      :not(:last-child) {
        margin-right: 0.5rem;
      }

      :hover {
        color: aliceblue;
      }
      a {
        :hover {
          color: aliceblue;
        }
        transition: all 0.2s ease-in-out;
      }
      transition: all 0.2s ease-in-out;
    }
  }
`;

export class NavMenu extends React.Component {
  render() {
    return (
      <Nav>
        <ul>
          <li>
            <Link to={"/posts"}>글</Link>
          </li>
          <li>
            <Link to={"/music"}>음악</Link>
          </li>
        </ul>
      </Nav>
    );
  }
}
